}
.navigation .col-sm-12 {
    display: flex;
    justify-content: space-between;
}


 
#navigation, #navigation ul, #navigation ul li, #navigation ul li a, #navigation #menu-button {
    margin: 0;
    padding: 0;
    border: 0;
    list-style: none;
    line-height: 1;
    display: block;
    position: relative;
    box-sizing: border-box;
    float: left;
}
#navigation:after, #navigation > ul:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
}
#navigation #menu-button {
    display: none;
}
#navigation > ul > li {
    float: left;
}
#navigation.align-center > ul {
    font-size: 0;
    text-align: center;
}
#navigation.align-center > ul > li {
    display: inline-block;
    float: none;
}
#navigation.align-center ul ul {
    text-align: left;
}
#navigation.align-right > ul > li {
    float: right;
}
#navigation > ul > li > a {
    padding: 32px 0px 32px 37px;
    letter-spacing: 1px;
    text-decoration: none;
    color: #333;
    margin-right: -1px;
    font-weight: 700;
}

#navigation > ul > li.menuItemHasChildren > a {
    padding: 32px 14px 32px 37px;
}
#navigation > ul > li.menuItemHasChildren > a::before {
    position: absolute;
    right: -1px;
    font-family: FontAwesome;
    content: "\f107";
    transition: all .25s ease;
    margin-top: 1px;
}

#navigation ul ul li.menuItemHasChildren > a::before {
    position: absolute;
    top: 16px;
    right: 14px;
    display: block;
    content: '';
    transition: all .25s ease;
    content: "\f105";
    font-family: FontAwesome;
}
#navigation > ul > li > .active {
    color: #E33D46;
}
#navigation > ul > li:hover > a {
    color: #E33D46;
}
#navigation ul ul {
    position: absolute;
    left: -9999px;
    top: 74px;
    background: #fff;
}
#navigation.align-right ul ul {
    text-align: right;
}
#navigation ul ul li {
    height: 0;
    transition: all .25s ease;
}
#navigation li:hover > ul {
    left: auto;
}
#navigation.align-right li:hover > ul {
    left: auto;
    right: 0;
}
#navigation li:hover > ul > li {
    height: 43px;
}
#navigation ul ul ul {
    margin-left: 100%;
    top: 0;
}
#navigation.align-right ul ul ul {
    margin-left: 0;
    margin-right: 100%;
}
#navigation ul ul li a {
    padding: 14px 15px;
